/**
 * Daemon lifecycle management
 *
 * Handles PID tracking, daemon startup, and process detection
 */

import { exec, spawn } from "node:child_process";
import { readFileSync } from "node:fs";
import { readFile, stat, unlink, writeFile } from "node:fs/promises";
import { dirname, join } from "node:path";
import { fileURLToPath } from "node:url";
import { promisify } from "node:util";
import {
  CLAUDE_ZEST_DIR,
  DAEMON_FRESH_PID_THRESHOLD_MS,
  DAEMON_PID_FILE,
} from "../config/constants.js";
import { cleanupStaleLocks, withFileLock } from "./file-lock.js";
import { ensureDirectory } from "./fs-utils.js";
import { logger } from "./logger.js";

// Lock file to prevent concurrent daemon restarts
const DAEMON_RESTART_LOCK = join(CLAUDE_ZEST_DIR, "daemon-restart.lock");

const __filename = fileURLToPath(import.meta.url);
const __dirname = dirname(__filename);

/**
 * Check if a process is running (cross-platform)
 * Uses signal 0 which checks existence without actually sending a signal
 */
export function isProcessRunning(pid: number): boolean {
  try {
    process.kill(pid, 0);
    return true;
  } catch {
    return false;
  }
}

/**
 * Start the sync daemon as a detached background process
 */
export async function startDaemon(): Promise<boolean> {
  try {
    // Find the daemon script path
    const daemonScript = join(__dirname, "..", "sync-daemon.js");

    // Spawn daemon as detached background process
    const daemon = spawn(process.execPath, [daemonScript], {
      detached: true, // Run independently
      stdio: "ignore", // Don't capture output
      windowsHide: true, // Hide console window on Windows
    });

    // Unref so parent can exit without waiting for daemon
    daemon.unref();

    // Daemon writes its own PID file when it starts
    // Wait a moment to ensure it's written
    await new Promise((resolve) => setTimeout(resolve, 100));

    return true;
  } catch (error) {
    logger.error("Failed to start daemon:", error);
    return false;
  }
}

/**
 * Kill all running sync-daemon processes
 * This ensures we don't accumulate zombie daemons
 */
async function killAllDaemons(): Promise<void> {
  try {
    // First try to kill the daemon from PID file
    const pid = await getDaemonPid();
    if (pid) {
      try {
        process.kill(pid, "SIGTERM");
      } catch {
        // Process might not exist
      }
    }

    // Also try to kill any other sync-daemon processes using pkill
    // This catches zombies that weren't tracked in PID file
    const execAsync = promisify(exec);

    try {
      // Kill all sync-daemon.js processes (excluding our own process)
      await execAsync(`pkill -f 'sync-daemon.js' 2>/dev/null || true`);
    } catch {
      // pkill might not find any processes, which is fine
    }

    // Wait for processes to terminate
    await new Promise((resolve) => setTimeout(resolve, 500));

    // Clean up PID file
    await cleanupPidFile();
  } catch (error) {
    logger.warn("Error during daemon cleanup:", error);
  }
}

/**
 * Restart daemon - kills ALL existing daemons and starts a fresh one
 * Uses file locking to prevent multiple hooks from starting multiple daemons
 */
export async function restartDaemon(): Promise<boolean> {
  try {
    // Use file lock to ensure only ONE process can restart the daemon at a time
    return await withFileLock(DAEMON_RESTART_LOCK, async () => {
      // Check if a daemon was JUST started (within threshold)
      // This prevents the second hook from killing the daemon the first hook just started
      const existingPid = await getDaemonPid();
      if (existingPid) {
        try {
          const pidFileStat = await stat(DAEMON_PID_FILE);
          const ageMs = Date.now() - pidFileStat.mtimeMs;

          if (ageMs < DAEMON_FRESH_PID_THRESHOLD_MS) {
            // PID file is fresh - daemon was just started by another hook
            return true;
          }
        } catch {
          // Ignore errors checking PID file age
        }
      }

      // Kill ALL existing daemon processes (not just the one in PID file)
      await killAllDaemons();

      // Clean up any stale lock files from crashed processes
      await cleanupStaleLocks();

      // Start new daemon
      return await startDaemon();
    });
  } catch (error) {
    logger.error("Failed to restart daemon:", error);
    return false;
  }
}

/**
 * Clean up stale PID file
 */
async function cleanupPidFile(): Promise<void> {
  try {
    await unlink(DAEMON_PID_FILE);
  } catch {
    // Ignore errors - file might not exist
  }
}

/**
 * Write PID to daemon.pid file
 * Called by the daemon itself when it starts
 */
export async function writePidFile(pid: number): Promise<void> {
  try {
    await ensureDirectory(dirname(DAEMON_PID_FILE));
    await writeFile(DAEMON_PID_FILE, pid.toString(), "utf-8");
    logger.debug(`Wrote PID ${pid} to daemon.pid`);
  } catch (error) {
    logger.error("Failed to write PID file:", error);
  }
}

/**
 * Get daemon PID (if running)
 */
export async function getDaemonPid(): Promise<number | null> {
  try {
    const pidData = await readFile(DAEMON_PID_FILE, "utf-8");
    const pid = Number.parseInt(pidData.trim(), 10);

    if (Number.isNaN(pid)) {
      return null;
    }

    // Verify process exists
    return isProcessRunning(pid) ? pid : null;
  } catch {
    return null;
  }
}

/**
 * Check if daemon is running (synchronous version for statusline)
 * Verifies both PID file existence and process health
 */
export function isDaemonRunning(): boolean {
  try {
    const pidData = readFileSync(DAEMON_PID_FILE, "utf-8");
    const pidStr = pidData.trim();

    // Check for empty PID file
    if (!pidStr) {
      return false;
    }

    const pid = Number.parseInt(pidStr, 10);

    // Check for invalid PID (NaN or non-positive)
    if (Number.isNaN(pid) || pid <= 0) {
      return false;
    }

    // Verify process exists
    return isProcessRunning(pid);
  } catch {
    // PID file doesn't exist or can't be read
    return false;
  }
}
