/**
 * Cross-process file locking utility
 *
 * Provides file-based locking to prevent corruption from concurrent access
 * across different processes (plugin hooks vs daemon)
 *
 * Uses PID-based stale lock detection - a lock is only considered stale
 * if the owning process is dead, allowing long-running operations.
 */

import { unlinkSync } from "node:fs";
import { readdir, readFile, unlink, writeFile } from "node:fs/promises";
import { dirname } from "node:path";
import { LOCK_MAX_RETRIES, LOCK_RETRY_MS, QUEUE_DIR } from "../config/constants.js";
import { isProcessRunning } from "./daemon-manager.js";
import { ensureDirectory } from "./fs-utils.js";
import { logger } from "./logger.js";

/**
 * Lock file metadata - stored in .lock files
 */
interface LockInfo {
  pid: number;
  timestamp: number; // For debugging/logging only - PID is authoritative
}

// Track active lock files for cleanup on process exit
const activeLockFiles = new Set<string>();

/**
 * Check if a lock file represents a stale lock
 * A lock is stale ONLY if the owning process is dead
 * This ensures long-running operations (like uploads) don't have their locks stolen
 */
function isLockStale(lockInfo: LockInfo): boolean {
  return !isProcessRunning(lockInfo.pid);
}

/**
 * Acquire a file-based lock (cross-process safe)
 * Uses atomic file creation with 'wx' flag to prevent race conditions
 *
 * @returns true if lock was acquired, false if held by another active process
 */
async function acquireFileLock(filePath: string): Promise<boolean> {
  const lockFile = `${filePath}.lock`;
  const lockInfo: LockInfo = {
    pid: process.pid,
    timestamp: Date.now(),
  };

  try {
    // Ensure parent directory exists before creating lock file
    await ensureDirectory(dirname(lockFile));

    // 'wx' flag = exclusive create - fails atomically if file already exists
    await writeFile(lockFile, JSON.stringify(lockInfo), { flag: "wx" });
    activeLockFiles.add(lockFile);
    return true;
  } catch (error) {
    if ((error as NodeJS.ErrnoException).code !== "EEXIST") {
      // Log more context for directory-related errors
      const errCode = (error as NodeJS.ErrnoException).code;
      if (errCode === "ENOENT" || errCode === "EACCES") {
        logger.error(`Failed to create lock file ${lockFile}:`, error);
      }
      throw error; // Unexpected error - rethrow
    }

    // Lock file exists - check if it's stale (owning process is dead)
    try {
      const content = await readFile(lockFile, "utf8");
      const existingLock: LockInfo = JSON.parse(content);

      if (isLockStale(existingLock)) {
        // Remove stale lock and retry
        logger.debug(`Removing stale lock for ${filePath} (PID ${existingLock.pid} is dead)`);
        await unlink(lockFile).catch(() => {});
        return acquireFileLock(filePath); // Recursive retry
      }
    } catch {
      // Couldn't read/parse lock file - it may be corrupted or being written
      // Remove it and retry
      logger.debug(`Lock file for ${filePath} is corrupted or unreadable, removing`);
      await unlink(lockFile).catch(() => {});
      return acquireFileLock(filePath);
    }

    return false; // Lock is held by an active process
  }
}

/**
 * Release a file-based lock
 */
async function releaseFileLock(filePath: string): Promise<void> {
  const lockFile = `${filePath}.lock`;
  activeLockFiles.delete(lockFile);
  await unlink(lockFile).catch(() => {}); // Ignore errors - file might already be gone
}

/**
 * Cleanup handler for graceful shutdown
 * Removes any lock files this process holds
 */
function cleanupLockFiles(): void {
  for (const lockFile of activeLockFiles) {
    try {
      unlinkSync(lockFile); // Must be sync in exit handler
    } catch {
      // Ignore errors during cleanup
    }
  }
  activeLockFiles.clear();
}

/**
 * Clean up any stale lock files in the queue directory
 * Called during daemon restart to remove orphaned locks from crashed processes
 */
export async function cleanupStaleLocks(): Promise<void> {
  try {
    const files = await readdir(QUEUE_DIR).catch(() => [] as string[]);
    const lockFiles = files.filter((f) => f.endsWith(".lock"));

    for (const lockFileName of lockFiles) {
      const lockFile = `${QUEUE_DIR}/${lockFileName}`;
      try {
        const content = await readFile(lockFile, "utf8");
        const lockInfo: LockInfo = JSON.parse(content);

        if (!isProcessRunning(lockInfo.pid)) {
          await unlink(lockFile);
          logger.info(`Cleaned up stale lock file: ${lockFileName} (PID ${lockInfo.pid} is dead)`);
        }
      } catch {
        // Corrupted lock file - remove it
        await unlink(lockFile).catch(() => {});
        logger.info(`Removed corrupted lock file: ${lockFileName}`);
      }
    }
  } catch (error) {
    logger.debug("Failed to clean up stale locks:", error);
  }
}

// Set up cleanup handlers for graceful shutdown
let cleanupRegistered = false;

/**
 * Register process exit handlers to clean up lock files
 * Safe to call multiple times - only registers once
 */
export function setupLockCleanup(): void {
  if (cleanupRegistered) return;
  cleanupRegistered = true;

  process.on("exit", cleanupLockFiles);
  process.on("SIGINT", () => {
    cleanupLockFiles();
    process.exit(0);
  });
  process.on("SIGTERM", () => {
    cleanupLockFiles();
    process.exit(0);
  });

  logger.debug("Lock cleanup handlers registered");
}

/**
 * Execute a function with exclusive file-based lock
 * Provides cross-process protection for file operations
 *
 * @param filePath - Path to the file to lock (creates filePath.lock)
 * @param fn - Function to execute while holding the lock
 * @returns The result of the function
 * @throws Error if lock cannot be acquired after max retries
 */
export async function withFileLock<T>(filePath: string, fn: () => Promise<T>): Promise<T> {
  // Acquire file-based lock with retry
  let retries = 0;
  while (!(await acquireFileLock(filePath))) {
    if (++retries >= LOCK_MAX_RETRIES) {
      throw new Error(`Failed to acquire lock for ${filePath} after ${retries} retries`);
    }
    await new Promise((resolve) => setTimeout(resolve, LOCK_RETRY_MS));
  }

  try {
    return await fn();
  } finally {
    await releaseFileLock(filePath);
  }
}
